// File: i18n.js

import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import LanguageDetector from "i18next-browser-languagedetector";

// import translations file
import { TRANSLATIONS_TH } from 'translations/th/translations'
import { TRANSLATIONS_EN } from 'translations/en/translations'

const LanguageRequire=JSON.parse(localStorage.getItem("ThaiLanguage"))
console.log(LanguageRequire)
const resources= LanguageRequire==="th"?{th:{translation: TRANSLATIONS_TH}}:{en:{translation: TRANSLATIONS_EN}}

console.log(resources)

i18n
    .use(initReactI18next)
    .init({
	debug: true,
	initImmediate: false,
	preload: ["th", "en"],
	fallbackLng: LanguageRequire,
	lng: LanguageRequire,
    resources
});


