import React from "react"
import clsx from "clsx"
import { Link } from "react-router-dom";
import { useTheme } from '@material-ui/core/styles';
import {Avatar, Box, Divider, IconButton, Grid, List} from '@material-ui/core'
import {ListItem, ListItemIcon, ListItemText, Drawer, Typography} from "@material-ui/core"
import { red, blue} from '@material-ui/core/colors'
import VpnKeyIcon from '@material-ui/icons/VpnKey';
import HomeIcon from '@material-ui/icons/Home';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import DashboardIcon from '@material-ui/icons/Dashboard';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import VpnKeyOutlinedIcon from '@material-ui/icons/VpnKeyOutlined';

const MyDrawer = ({ title, open, onClose, onItemClick, variant, t}) => {
  const user = JSON.parse(localStorage.getItem('user'));
  const theme=useTheme

  
  const PublicList=()=>{
	  return (
	  <React.Fragment>
	  		<Box mt={2} />
		<Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
		  <Avatar alt='login' >
			  <VpnKeyIcon color="primary" />
		  </Avatar>
		  <Avatar alt='login' >
			  {t(title)}
		  </Avatar>
		  <Avatar> {title.slice(0, 1)}</Avatar>
		</Grid> 
		<Box mt={2} />
	  <Divider />
		<div style={{width: 250}}>
		   {[{title:'Home', thisLink:'/'},
			 {title:'Login', thisLink:'/login'},
		     {title:'Signup', thisLink:'/signup'}].map((text, index) => (
            <ListItem 
				button 
				key={text.title} 
				component={Link} 
			    to={text.thisLink}
				onClick={onItemClick(text.title)} 
			>
					{[<HomeIcon style={{ color: red[500] }}/>, 
					  <VpnKeyOutlinedIcon style={{ color: blue[500] }} />, 
					  <ExitToAppIcon color="secondary"/>].map((thisIcon, k)=>
						k===index&&(<ListItemIcon >{thisIcon} </ListItemIcon>)
					)}
				 <ListItemText secondary=
					{<React.Fragment> 	
						<Typography
							component="span"
							variant="h6"
							color="textPrimary"
						>
							{t(text.title)} 
						</Typography>
					</React.Fragment>
			}  />
			</ListItem>
		   ))}
		 </div>
        <Divider />
	  </React.Fragment>
	  )
  }

  const AuthList=()=>{
	  return (
	  <React.Fragment>
	  		<Box mt={2} />
		<Grid
			container
			spacing={1}
			justify="center"
			alignItems="center"
		>
		  <Avatar alt={user.user.firstname} style={{ backgroundColor: blue[500] }}>
			 {user.user.firstname}
		  </Avatar>
		  <Typography>
		  &nbsp;{user.user.email}
		  </Typography>
		</Grid> 
		<Box mt={2} />
	  <Divider />
		<div style={{width: 250}}>
		     {[{title:'Dashboard', thisLink:'/dashboard'},
			 {title:'Show Clinic members', thisLink:'/clinicmembers'},
		     {title:'Show Child and Family', thisLink:'/patients'},
			 {title:'Show collection OPDscreen', thisLink:'/opdscreens'}].map((text, index) => (
            <ListItem 
				button 
				key={text.title} 
				component={Link} 
			    to={text.thisLink}
				onClick={onItemClick(text.title)} 
			>
					{[<DashboardIcon style={{ color: red[500] }}/>, 
					  '\u{1F930}',
					  '\u{1F46A}', 
					  '\u{1F46A}'].map((thisIcon, k)=>
						k===index&&(<ListItemIcon >{thisIcon} </ListItemIcon>)
					)}
				 <ListItemText secondary=
					{<React.Fragment> 	
						<Typography
							component="span"
							variant="h6"
							color="textPrimary"
						>
							{t(text.title)} 
						</Typography>
					</React.Fragment>
			}  />
			</ListItem>
		   ))}
			</div>
        <Divider />

			</React.Fragment>
	  )
  }

  
  return (

	  <Drawer
        open={open}
		onClose={onClose}
      >	
		<List disablePadding>
			{user?
			<AuthList/>:
			<PublicList/>
			}
		</List>
      </Drawer>
  )
};

export default MyDrawer;
