import React, { Suspense, lazy } from "react"
import { Route, Switch } from "react-router-dom"
import { useTheme } from '@material-ui/core/styles';
import {Box, Container, Grid, Paper} from "@material-ui/core";
import CircularProgressWithLabel from 'component/CircularProgress'

const Home = lazy(() => import("pages/Home"))
const Login = lazy(() => import('pages/Login'))
const Signup = lazy(() => import('pages/Signup')) 
const Dashboard = lazy(() => import('pages/dashboard/Dashboard')) 
const Profile = lazy(() => import('pages/Profile')) 
const Tutorials = lazy(() => import('crud/Tutorial'))
const TutorialsList = lazy(() => import('crud/TutorialsList'))
const AddTutorial = lazy(() => import('crud/AddTutorial'))
const BlogPost = lazy(() => import('crud/BlogPost'))


const MyMain = ({searchTitle, page, setPage, t}) => {
	const theme=useTheme()
	return ( 
      <main>
		<div/>
		<Container  style={{
			paddingTop: "30dp",
			width: 'auto',
			marginLeft: theme.spacing(0),
			marginRight: theme.spacing(1),
			[theme.breakpoints.up(600 + theme.spacing(1) * 2)]: {
			  width: 600,
			  marginLeft: 'auto',
			  marginRight: 'auto'}
			  }}>

		    <Grid container spacing={2}  direction="row" justify="center" alignItems="center">
						<Suspense fallback={<div><CircularProgressWithLabel/></div>}>
						<Switch>
						  <Route exact path="/" component={Home} />
						  <Route exact path="/home" component={Home} />
						  <Route path="/signup" component={Signup} />		 
						  <Route path="/login" component={Login} />
						  <Route path="/dashboard" component={Dashboard} />
						  <Route path="/profile" component={Profile} />
						  <Route path="/patients" render={() => <TutorialsList searchTitle={searchTitle} page={page} setPage={setPage} thisCollection="patients"  t={t} />} />
						  <Route exact path="/add" component={AddTutorial} />
						  <Route path="/opdscreens"render={() => <TutorialsList searchTitle={searchTitle} page={page} setPage={setPage} thisCollection="opdscreens"  t={t} />} />
						  <Route path="/clinicmembers"render={() => <TutorialsList searchTitle={searchTitle} page={page} setPage={setPage} thisCollection="clinicmembers" />} />
						  <Route path="/persons"render={() => <TutorialsList searchTitle={searchTitle} page={page} setPage={setPage} thisCollection="persons"  t={t}/>} />
						 <Route>
							<Home/>
						 </Route>
						</Switch>
						</Suspense>
			</Grid>
			</Container>
      </main>
	  )
  }

export default MyMain
