import React, {useState} from 'react';
import { HashRouter as Router, useHistory} from "react-router-dom";
import CssBaseline from "@material-ui/core/CssBaseline";
import { createMuiTheme, ThemeProvider } from "@material-ui/core/styles";
import { useTranslation } from "react-i18next";
import 'translations/i18n.js';

import MyFrame from "component/MyFrame"
import MyDrawer from "component/MyDrawer"
import MyMain from "component/MyMain"

const App=({variant})=> {
  const history=useHistory()
  const [drawer, setDrawer] = useState(false);
  const [title, setTitle] = useState("Home");
  const [searchTitle, setSearchTitle] = useState("")
  const [page, setPage] = useState(1)
  const { t } = useTranslation();
  const [isThai, setIsThai] = useState(JSON.parse(localStorage.getItem("ThaiLanguage")))
  const [darkState, setDarkState] = useState(false);
  const palletType = darkState ? "dark" : "light";
  const mainPrimaryColor = darkState ? "#455A64" : "#2196f3";
  const mainSecondaryColor = darkState ? "#29B6F6" : "#4DD0E1";
  const darkTheme = createMuiTheme({
    palette: {
      type: palletType,
      primary: {
        main: mainPrimaryColor
      },
      secondary: {
        main: mainSecondaryColor
      }
    }
  });
  const handleThemeChange = () => {
    setDarkState(!darkState);
  };
  
   const handleLanguage = ()=> {
	  localStorage.setItem("ThaiLanguage", JSON.stringify(isThai==='th'?'en':'th'));	  
	  setIsThai(isThai==='th'?'en':'th')
	  window.location.reload()
  }
  
  const toggleDrawer = () => {
    setDrawer(!drawer);
  };
  const onItemClick = title => () => {
    setTitle(title);
    setDrawer(variant === "temporary" ? false : drawer);
    setDrawer(!drawer);
  };

  return (
    <div>
	  <ThemeProvider theme={darkTheme}>
	  <CssBaseline />
		<Router >
		  <MyFrame 
					title={title} 
					searchtitle={searchTitle}
					setSearchTitle={setSearchTitle}
					page={page}
					setPage={setPage}
					onMenuClick={toggleDrawer}
					darkState={darkState}
					handleThemeChange={handleThemeChange}
					isThai={isThai}
					t={t}
					handleLanguage={handleLanguage}	
			/>
		  <MyDrawer
				title={title}
				open={drawer}
				onClose={toggleDrawer}
				onItemClick={onItemClick}
				variant={variant}
				t={t}
			/>		
		  <MyMain
			    searchTitle={searchTitle}
				page={page}
				setPage={setPage}
				t={t}
		  />
			   
		</Router>
	  </ThemeProvider>
    </div>
  );
}

export default App;
